.button {
  border: none;
  border-radius: 1vw;
  width: 7vw;
  margin: 0.5vw;
  height: 2.5vw;
  cursor: pointer;
  background-color: #868e96;
  transition: background-color 0.2s;
}

.button:hover {
  background-color: #9ca3af;
  transition: background-color 0.2s;
}

.save-customer-button {
  background-color: #2f9e44;
  transition: background-color 0.2s;
}

.save-customer-button:hover {
  background-color: #2b8a3e;
  transition: background-color 0.2s;
}

.save-update-button {
  margin: 0;
  width: 5vw;
  margin-right: 0.5vw;
  height: 2.3vw;
  background-color: #2f9e44;
  transition: background-color 0.2s;
}

.save-update-button:hover {
  background-color: #2b8a3e;
  transition: background-color 0.2s;
}

.cancel-update-button {
  margin: 0;
  width: 5vw;
  height: 2.3vw;
}

.disabled {
  pointer-events: none;
  opacity: 0.8;
}
