* {
  margin: 0;
  padding: 0;
  font-size: 1.3vw;
  font-family: "Lato", sans-serif;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #272727;
}

section {
  margin: 1vw auto;
  width: 75vw;
  padding: 2vw;
  border-radius: 1vw;
  text-align: center;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
