.table-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 35vw;
}

.customer-table {
  border-collapse: collapse;
  width: 70vw;
  align-self: center;
  margin-top: 2.5vw;
}

.table-header {
  width: 17%;
}

.table-header--number {
  width: 2%;
}

.table-header--action {
  width: 13%;
}

.table-row:nth-child(even) {
  background: rgb(255, 255, 255);
}
.table-row:nth-child(odd) {
  background: rgb(241, 240, 240);
}
