.button-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 9vw;
}

.modal-box {
  position: fixed;
  top: 20vh;
  left: 30vw;
  width: 40vw;
  height: 15vw;
  background-color: white;
  padding: 1.5vw;
  border-radius: 2vh;
  z-index: 30;
  display: flex;
  flex-direction: column;
}

.message {
  align-self: center;
}
