@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.add-customer-link {
  background-color: #2f9e44;
  text-decoration: none;
  width: 7vw;
  margin: 0.5vw;
  height: 2.5vw;
  border-radius: 1vw;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: black;
  transition: background-color 0.2s;
}

.add-customer-link:hover {
  background-color: #2b8a3e;
  transition: background-color 0.2s;
}

.search-input {
  overflow: hidden;
  height: 2vw;
  border-radius: 1vw;
  border-width: 1px;
  margin: 0.5vw;
  padding-left: 0.8vw;
}

.top-container {
  align-self: flex-end;
  display: flex;
  align-items: flex-end;
  margin-right: 2.5vw;
}

.table-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 35vw;
}
