.add-form {
  align-self: center;
  display: flex;
  flex-direction: column;
  width: 30vw;
}

.form-group {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text {
  overflow: hidden;
  white-space: nowrap;
  align-self: flex-start;
  margin-left: 3vw;
}

.add-input {
  overflow: hidden;
  height: 2vw;
  border-radius: 1vw;
  border-width: 1px;
  padding-left: 0.8vw;
  width: 24vw;
  margin: 0.5vw;
}

.input-required {
  border: solid 1px red;
  background-color: #fee2e2;
}

.button-container {
  width: 30vw;
  display: flex;
  flex-direction: row;
  margin-top: 0.5vw;
  justify-content: center;
  align-self: center;
}

.back-link {
  background-color: #868e96;
  text-decoration: none;
  color: black;
  width: 7vw;
  margin: 0.5vw;
  line-height: 2vw;
  border-radius: 1vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.back-link:hover {
  background-color: #9ca3af;
}

.success-message {
  width: 20vw;
  height: 2.5vw;
  margin-bottom: 1vw;
}
